import uniIcon from "@/components/uni-icons/uni-icons.vue"
export default {
	data() {
		return {
			yonghuwx: []
		}
	},
	components: {
		uniIcon
	},
	methods: {
		toAddress() {//收货地址
			uni.navigateTo({
				url:"address/address"
			})
		},
		toCoupon() {//优惠券
			uni.navigateTo({
				url:"coupon/coupon"
			})
		},
		toUserMsg() {//用户中心
			uni.navigateTo({
				url:"userMsg/userMsg"
			})
		},
		toSale() {//售后
			uni.navigateTo({
				url:"afterSale/afterSale"
			})
		},
		toAllOrder() {//全部订单
			uni.navigateTo({
				url:"order/order"
			})
		}
	},
	onLoad() {
		let that = this;
		uni.login({
			provider: 'weixin',
			success: function(loginRes) {
				// 获取用户信息				
				uni.getUserInfo({
					provider: 'weixin',
					success: function(infoRes) {
						that.yonghuwx = infoRes.userInfo
					}
				});
			}
		});
	}
}
